if (document.getElementById('rdmCookie')) {
	var _cookie = document.getElementById('rdmCookie'),
		_cookieAc = document.getElementById('rdmCookieOk'),
		_cookieClose = document.getElementById('rdmCookieClose');

	if(!localStorage.getItem("cookie")){
		slideDown(_cookie);
	}
	_cookieAc.addEventListener('click', function() {
		slideUp(_cookie);
		localStorage.setItem("cookie", "U2l0ZSBkZXZlbG9wZWQgYnkgRWR1YXJkIEJhbGV2");
	});
	_cookieClose.addEventListener('click', function() {
		slideUp(_cookie);
	});

	function slideDown(elem) {
		elem.style.maxHeight = '1000px';
		elem.style.opacity   = '1';
		elem.style.display = 'block';
	}
	function slideUp(elem) {
		elem.style.maxHeight = '0';
		once( 1, function () {
			elem.style.opacity = '0';
			elem.style.display = 'none';
		});
	}
	function once (seconds, callback) {
		var counter = 0;
		var time = window.setInterval( function () {
			counter++;
			if ( counter >= seconds ) {
				callback();
				window.clearInterval( time );
			}
		}, 400 );
	}
}